import "./wallet.scss";
import { FC, useState } from "react";
import { PropsWalletProvider } from "../utils";
import { MetaMaskInpageProvider } from "@metamask/providers";

const WalletProvider: FC<PropsWalletProvider> = ({ onChange, state, connectHandler }) => {
  const [connectStatus, setConnectStatus] = useState<any>(false);

  return (
    <div
      className="connect-btn rightPinImage"
      data-scroll
      data-scroll-sticky
      // data-scroll-speed="10"
      data-scroll-target="#js-scroll"
      onClick={() => {
        onChange((prevState: any) => !prevState);
        setConnectStatus(connectHandler());
        console.log(connectHandler());
        console.log(connectStatus);
      }}
    >
      <div className="StateText">{connectStatus === true ? "disconnect wallet" : "connect wallet"}</div>
    </div>
  );
};

export default WalletProvider;
