import "./LocomotiveScrollProvider.css";
import { useLocomotiveScroll } from "react-locomotive-scroll";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocomotiveScroll from "locomotive-scroll";
import React, { useEffect, useState } from "react";
import WalletProvider from "../walletProvider/walletProvider";
import { ethers } from "ethers";

gsap.registerPlugin(ScrollTrigger);

export default function LocomotiveScrollProvider({ children }) {
  const [errorMessage, setErrorMessage] = useState(null);
  const [account, setAccount] = useState(null);
  const [balance, setBalance] = useState(null);

  useEffect(() => {
    console.log(balance);
  }, [balance]);
  useEffect(() => {
    console.log(account);
  }, [account]);

  const connectHandler = async () => {
    if (window.ethereum) {
      try {
        const res = await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        await accountsChanged(res[0]);
        if (res) {
          return true;
        } else {
          return false;
        }
      } catch (err) {
        console.error(err);
        setErrorMessage("There was a problem connecting to MetaMask");
        return false;
      }
    } else {
      setErrorMessage("Install MetaMask");
      return false;
    }
  };

  const accountsChanged = async (newAccount) => {
    setAccount(newAccount);
    try {
      const balance = await window.ethereum.request({
        method: "eth_getBalance",
        params: [newAccount.toString(), "latest"],
      });

      setBalance(ethers.utils.formatEther(balance));
    } catch (err) {
      console.error(err);
      setErrorMessage("There was a problem connecting to MetaMask");
    }
  };

  const chainChanged = () => {
    setErrorMessage(null);
    setAccount(null);
    setBalance(null);
  };

  useEffect(() => {
    document.body.scrollTo(0, 0);
    const locoScroll = new LocomotiveScroll({
      el: document.body,
      smooth: true,
      getSpeed: true,
      getDirection: true,
      inertia: 0.55,
      useKeyboard: true,
      tablet: { smooth: true },
      smartphone: { smooth: true },
      // offset: ["100%", 0],
    });

    locoScroll.update();
    locoScroll.on("scroll", ScrollTrigger.update);

    ScrollTrigger.scrollerProxy(document.body, {
      scrollTop(value) {
        return arguments.length ? locoScroll.scrollTo(value, 0, 0) : locoScroll.scroll.instance.scroll.y;
      },
      getBoundingClientRect() {
        return {
          top: 0,
          left: 0,
          width: window.innerWidth,
          height: window.innerHeight,
        };
      },
      pinType: document.body.style.transform ? "transform" : "fixed",
    });

    ScrollTrigger.addEventListener("refresh", () => locoScroll.update());
    ScrollTrigger.refresh();

    if (window.ethereum) {
      window.ethereum.on("accountsChanged", accountsChanged);
      window.ethereum.on("chainChanged", chainChanged);
    }
  }, []);

  const [state, setConect] = useState(false);

  function onChange(value) {
    setConect(value);
  }

  return (
    <div className="LocomotiveScrollProvider">
      <WalletProvider onChange={onChange} state={state} connectHandler={connectHandler} />
      {children}
    </div>
  );
}
